// 파일 쪼갤 때 활용하는 import / export
/* 
var name = 'Kim';
export default name; -> 변수를 내보냄 
: 페이지 마지막에 쓰고
: 페이지 하나 당 딱 한 번 작성 가능

import ??? from '.data.js'; -> data.js에 있는 ???를 사용할 수 있음
* ./ 가 현재경로임
import name from '.data.js';
name이라는 변수로 해당 파일에서 다른 파일에 있는 변수를 사용할 수 있음
: ???는 내가 원하는 변수로 작성 가능
*/
/*
내보낼 변수가 많다면 어떻게?
var name = 'Kim';
var name2 = 'Kim';

내보내기 : export { name, name2 } 
가져오기 : import { 변수1, 변수2 } from 경로
가져오기는 무조건 import { name, name2 } from 경로

export로 내보내면 무조건 가져올 때 그 해당 변수 명으로 가져와야함
*/ 