import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';



function Detail(props) {

  let { id } = useParams(); // {} object 자료형이 남음 {사용자가 입력한 URL 파라미터들}
  let history = useHistory();
  let 찾은상품 = props.shoes.find(function(상품){
    return 상품.id == id
  });

  const [alert, setAlert] = useState(true);
  const [input, setInput] = useState('');

  function 재고빼기() {
    let 재고복사본 = [...props.재고];
    재고복사본[id]--;
    props.재고변경(재고복사본);
  }

  useEffect(() => { 
    // 2초 후에 저거 alert 창을 안보이게 해주셈
    let 타이머 = setTimeout(() => { setAlert(false) }, 2000);
  }, []);

  return (
      <div className="container">
        <div>
          <h4 className='red'>Detail</h4>
        </div>

        { input }
        <input onInput={ (e) => { setInput(e.target.value) } } />

        {
          alert === true ?
          (<div className='my-alert2'>
            <p>재고가 얼마 남지 않았습니다.</p>
          </div>) : null
        }
        <div className="row">
          <div className="col-md-6">
            <img src={`https://codingapple1.github.io/shop/shoes${찾은상품.id + 1}.jpg`} width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
            <p>{찾은상품.price}원</p>
            <Info 재고={props.재고} id={id} />

            <button className="btn btn-danger" onClick={() => {
              재고빼기()
          }}>주문하기</button> 
            <button className="btn btn-danger" onClick={() => { history.goBack() }}>뒤로가기</button> 
          </div>
        </div>
      </div>  
  )
}

function Info(props) { 
  return (
    <p>재고 : {props.재고[props.id]} </p>
  )
}

export default Detail;